import React, { MouseEvent, useState } from 'react';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

export default function DownloadActions() {
    const [menuAnchorEl, setMenuAnchorEl] = useState<HTMLElement | null>(null);

    const handleClick = ({ currentTarget }: MouseEvent<HTMLElement>) => {
        setMenuAnchorEl(currentTarget);
    };

    const handleClose = () => {
        setMenuAnchorEl(null);
    };

    return (
        <div>
            <Button
                variant="text"
                size="small"
                color="inherit"
                endIcon={<KeyboardArrowDownIcon />}
                onClick={handleClick}
            >
                Downloads
            </Button>
            <Menu anchorEl={menuAnchorEl} open={Boolean(menuAnchorEl)} onClose={handleClose}>
                <MenuItem dense>Configuration file</MenuItem>
                <MenuItem dense>Image(s)</MenuItem>
            </Menu>
        </div>
    );
}
