import React from 'react';
import { useAppSelector } from '../redux';
import { useDispatch } from 'react-redux';
import TableContainer from '@mui/material/TableContainer';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';
import { setSelectedStation } from '../redux/app/action';

type DataTableFieldType<T> = {
    key: keyof T;
    label: string;
};

type StationDisplayData = {
    id: string;
    chineseName: string;
    englishName: string;
    interchange: string;
};

const fields: DataTableFieldType<StationDisplayData>[] = [
    { key: 'chineseName', label: 'Chinese name' },
    { key: 'englishName', label: 'English name' },
    { key: 'interchange', label: 'Interchange' },
];

export default function StationDataTable() {
    const dispatch = useDispatch();

    const selectedStation = useAppSelector(state => state.app.selectedStation);
    const stationList = useAppSelector(state => state.param.stn_list);

    const pageData: StationDisplayData[] = Object.entries(stationList)
        .filter(([id]) => !['linestart', 'lineend'].includes(id))
        .map(([id, station]) => {
            return {
                id,
                chineseName: station.name[0],
                englishName: station.name[1],
                interchange: station.transfer.info.map(info => info.length).join('-'),
            };
        });

    return (
        <TableContainer component={Paper} sx={{ flex: 1, width: 'unset'}}>
            <Table stickyHeader size="small">
                <TableHead>
                    <TableRow>
                        {fields.map(field => (
                            <TableCell key={field.key}>{field.label}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {pageData.map(item => (
                        <TableRow
                            key={item.id}
                            onClick={() => dispatch(setSelectedStation(item.id))}
                            tabIndex={-1}
                            selected={item.id === selectedStation}
                            hover
                        >
                            {fields.map(field => (
                                <TableCell key={item.id + '-' + field.key}>{item[field.key]}</TableCell>
                            ))}
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
